html, body {
    height: 100vh;
    width: 100vw;
}

.fade {
  animation: fadeInAnimation ease 5s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.App {
  background-color: rgba(228, 228, 228,1);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.text-on-image {
  position: absolute;
  left: 40%;
  bottom: 90%;
}

.image-on-image {
  position: relative;
  left: 43.6%;
  bottom: 125%;
}

.new-fade {
  position: relative;
  width: 48%;
  margin: auto;
  display: block;
}

.new-fade:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  background: linear-gradient(0deg,rgba(228, 228, 228,1),rgba(228, 228, 228,0));
  z-index: 3; 
  height: 20vh;
}

.fade-img {
  position: relative; 
}

.fade-img:after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    background: linear-gradient(0deg,rgba(228, 228, 228,1),rgba(228, 228, 228,0));
    z-index: 3; 
    height: 20vh;
}

.hr-thick {
    position: relative;
    height: 12px;
    background: #6C757D;
}